.flexbox {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.flexboxSearchGifs {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 20px;
    .searchGifForm {
        margin-left: 40px
    }
    .searchGifButton {
        padding: 10px;
        width: 100px;
        margin-left: 10px;
        font-size:20px;
    }
    input[type=text] {
        width: 300px;
        padding: 10px;
        font-size: 20px;
    }
    .glow-on-hover {
        font-size:20px;
        padding: 10px;
    }
    .viewMemesButtonContainer {
        margin-right: 40px
    }
}

.gifHeader {
    margin: 20px 0 0 40px;
    color: white;
    font-size: 20px;
    text-transform: capitalize;
}

.gifs {
    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    grid-auto-flow: row;
    gap: 25px;
    margin: 20px 1%;
    .gifContainer {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 18px;
        cursor: pointer;
        position: relative;
        z-index: 0;
        border-radius: 10px;
    }
    img {
        display: block;
        max-width: 100%;
    }
}

.eachMemeStyle {
    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    grid-auto-flow: row;
    gap: 25px;
    margin: 20px 1%;
    


    .eachMemeStyleContainer{
        padding: 18px;
        background: white;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

        img{
            display: block;
            max-width: 100%;
        }

        p{
            font-size: 18px;
            font-weight: bold;
            text-transform: uppercase;
            text-align: center;
            font-family: $heading;
            color: white;
            letter-spacing: 1.5px;
            margin-bottom: 10px;
            margin-top: 10px;
        } 
}

.creatingMemeText {
    font-size: 20px;
    text-align: center;
    margin-bottom: 10px;
}

.voteBtns{
    display: flex;
    justify-content: center;
    button{
        border-radius: 10px;
    }
    p{
        color: black;
    }
}

.noGifFoundText {
    text-align: center;
    font-size: 25px;
    color: $white;
    padding-bottom: 20px;
}

@media (max-width: 856px) {
    .createMemeSection {
        width: 300px;
    }
    
}

@media (max-width: 750px) {
    .gifHeader {
        text-align: center;
        margin: 20px 0px 0px;
    }
    .flexboxSearchGifs {
        flex-direction: column;
        align-items: center;
        margin: 0;
        text-align: center;
        .searchGifForm{
            flex-direction: column;
            align-items: center;
            margin: 0;
            .searchGifButton {
                margin-top: 10px;
            }
        }
        .viewMemesButtonContainer {
            margin: 10px 0 0;
        }
    }
}

.gifContainer:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}

.gifContainer {
  border: none;
  outline: none;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.gifContainer:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.gifContainer:active,
.gifContainer:focus {
  color: #000;
}

.gifContainer:hover:before {
  opacity: 1;
}